import React from 'react';


/**
 * 
 */
export default class TankComponent<P, S> extends React.Component <P, S> {

  //
  mounted: boolean = false

  componentWillUnmount() {
    let that = this
    that.mounted = false
  }

  componentWillMount() {
    let that = this
    that.mounted = true
  }

  //
  updateUI() {
    let that = this
    if (that.mounted) {
      that.setState({})
    } else {
      console.info(this.constructor.name + "")
    }
  }

  //
  getIdentifier() {
    return this.constructor.name;
  }
}

