/**
 * 
 */
export default class Filter {

  //
  name: string
  //
  key: string
  //
  visible: boolean = true

  constructor(name: string, key: string, visible?: boolean) {
    this.name = name
    this.key = key

    //true.
    if (visible === undefined) {
      this.visible = true
    } else {
      this.visible = visible
    }
  }

  /**
   * 
   */
  getValueString(): string {
    console.error(`${this.constructor.name}  getValue `)
    return ""
  }

  /**
   * value
   */
  putValue(value: string) {
    console.error(`${this.constructor.name}  putValue `)
  }

  /**
   * 
   */
  reset() {
    console.error(`${this.constructor.name}  reset `)
  }

  /**
   * 
   */
  isEmpty():boolean {
    console.error(`${this.constructor.name}  isEmpty `)
    return false
  }

}
