/**
 * 
 * 
 */
import Base from "../../base/Base";
import MimeUtil from "../../../util/MimeUtil";


export default class PreviewEngine extends Base {

  /**
   * url
   * {originUrl} urleg: https://tank.eyeblue.cn/api/alien/download/2a0ceee1-744c-4c82-4215-69c382597a50/abstract-free-photo-2210x1473.jpg
   * {url} publicUrl=originUrlpublicUrloriginUrldownloadToken eg: https://tank.eyeblue.cn/api/alien/download/2a0ceee1-744c-4c82-4215-69c382597a50/abstract-free-photo-2210x1473.jpg?downloadTokenUuid=6bdba52f-af6b-49ae-5a5d-fd80bfb01d3b
   */
  url: string = ""

  /**
   * 
   * *
   * .   xls,doc,ppt,xlsx,docx,pptx
   */
  extensions: string = ""

  /**
   * 
   */
  previewInSite: boolean = true


  constructor() {
    super()
  }

  getForm(): any {
    return {
      url: this.url,
      extensions: this.extensions,
      previewInSite: this.previewInSite,
    };
  }

  //
  static defaultPreviewEngines(): PreviewEngine[] {

    let defaultEngines: PreviewEngine[] = []

    //office
    let officeEngine: PreviewEngine = new PreviewEngine()
    officeEngine.url = "https://view.officeapps.live.com/op/embed.aspx?src={url}"
    officeEngine.extensions = "doc,ppt,xls,docx,pptx,xlsx"
    officeEngine.previewInSite = true
    defaultEngines.push(officeEngine)

    //
    let globalEngine: PreviewEngine = new PreviewEngine()
    globalEngine.url = "{originUrl}"
    globalEngine.extensions = "*"
    globalEngine.previewInSite = true
    defaultEngines.push(globalEngine)

    return defaultEngines
  }

  //
  canPreview(fileName: string): boolean {
    let extension: string = MimeUtil.getExtensionWithoutDot(fileName)
    if (!extension) {
      return false
    }

    let canHandle = false
    if (this.extensions === "*") {
      canHandle = true
    } else {
      let parts: string [] = this.extensions.split(",")
      for (let part of parts) {
        if (extension === part.toLowerCase() || "." + extension === part.toLowerCase()) {
          canHandle = true
          break
        }
      }
    }

    return canHandle
  }

}




